import React from "react";
import ReactMarkdown from "react-markdown";

import _ from "lodash";
import { useParams } from "react-router-dom";
import { Alert, Jumbotron } from "react-bootstrap";
import JSONTable from "./JSONTable.js";

import {
  Plot,
  Loading,
  FORM_LONG_NAMES,
  FORM_DESCRIPTIONS,
  DISCLAIMER,
  PUBLIC_URL,
} from "./Statics.js";
import useWindowDimensions from "./useWindowDimensions.js";
import useFetch from "./useFetch.js";
import Home from "./Home.js";
import { trimLines, make_layout, plot_visible, WidthLimitedLayout } from "./util.js";

export default function RenderForm() {
  const { width } = useWindowDimensions();
  let { form, stat } = useParams();
  const url = `${PUBLIC_URL}${form}.json`;

  const { response, loading } = useFetch(url);

  if (loading || _.isEmpty(response.form)) {
    return <Loading />;
  }

  if (response.form === "latest") {
    return (
      <Home
        data={response.data}
        last_updated={response.last_updated}
        width={width}
        stat={stat}
      />
    );
  }

  return render_form(response.form, response.data, width);
}

function render_jumbo(form) {
  return (
    <Jumbotron>
      <h2>{FORM_LONG_NAMES[form]}</h2>
      {FORM_DESCRIPTIONS[form]}
    </Jumbotron>
  );
}
function render_form(form, data, width) {
  let rendered_graphs = _.map(data, (form_data) => {
    if (!plot_visible(form, form_data.id, width)) return null;
    if (form_data.id === "I-131") return render_jumbo("I-131");
    let fig = JSON.parse(form_data.figure);
    let sub_render = null;
    sub_render = render_sub_table(form_data.sub_table, form, form_data.id);
    let all_annotations = _.get(fig, "layout.annotations", []);
    let annotations = _.filter(all_annotations, { visible: false });

    return (
      <div key={form_data.id}>
        <h5>{form_data.title}</h5>
        <Plot
          data={fig.data}
          layout={make_layout(fig.layout, width, form_data.id)}
          style={form_data.style || {}}
          config={{ displayModeBar: false, showTips: false }}
        />
        {_.map(annotations, (ann, i) => {
          return <ReactMarkdown key={i} children={trimLines(ann.text)} />;
        })}
        {sub_render}
        <hr />
      </div>
    );
  });

  return (
    <WidthLimitedLayout>
      {form === "I-765" ? (
        <Alert variant="success">
          The Forms for the Work Permit (I-765) and Advance Parole (I-131) are
          combined on this page. Most people apply for both simultaneously, so
          the processing times and approval rates are similar.
        </Alert>
      ) : null}

      {render_jumbo(form)}
      {rendered_graphs}
      {DISCLAIMER}
    </WidthLimitedLayout>
  );
}

function render_sub_table(sub_table, form, id) {
  if (_.isEmpty(sub_table)) return null;
  let name = id === "status_box_plot" ? "Status" : "Service Center";

  return (
    <div>
      <p>
        {`This chart groups processing times for Form ${form} by ${name}.
  The "Processing Times" column in the table below shows the median and max quantiles, i.e.
  between the top 50% fastest case and the top 90% fastest case.`}
      </p>
      <JSONTable
        rows={sub_table}
        columns={[name, "# of Cases", "Processing Times"]}
        size="sm"
        bordered
      />
    </div>
  );
}
