import { Switch, Route, Redirect } from "react-router-dom";
import About from "./About.js";
import { FieldOffices, FieldOfficesRedirect } from "./FieldOffices.js";
import Rankings from "./Rankings.js";
import CaseSamples from "./CaseSamples.js";
import RenderForm from "./RenderForm.js";

export default function Routes() {
  return (
    <Switch>
      <Route path="/about">
        <About />
      </Route>
      <Route path="/rankings/:size">
        <Rankings />
      </Route>
      <Route path="/rankings">
        <Rankings />
      </Route>
      <Route path="/case_samples">
        <CaseSamples />
      </Route>
      <Route exact path="/">
        <Redirect to="/latest/cards_mailed" />
      </Route>
      <Route exact path="/latest/latest">
        <Redirect to="/" />
      </Route>
      <Route
        exact
        path="/field_offices"
        render={(props) => <FieldOfficesRedirect />}
      ></Route>
      <Route path="/field_offices/:location_code/:fo_name">
        <FieldOffices />
      </Route>
      <Route path="/:form/:stat">
        <RenderForm />
      </Route>
      <Route path="/:form">
        <RenderForm />
      </Route>
    </Switch>
  );
}
