import React from "react";

import _ from "lodash";
import { useParams, useHistory, Redirect } from "react-router-dom";
import { LinkContainer } from "react-router-bootstrap";
import { Card, Alert, Button, Form } from "react-bootstrap";

import { make_layout, slugify, WidthLimitedLayout } from "./util.js";

import { Plot, Loading, PUBLIC_URL, DISCLAIMER } from "./Statics.js";
import useWindowDimensions from "./useWindowDimensions.js";
import useFetch from "./useFetch.js";

import "./App.css";

function FieldOfficesRedirect() {
  const location_code = localStorage.getItem("fieldOfficeValue") || "BOS"; // Boston is default
  const location_label =
    localStorage.getItem("fieldOfficeLabel") || "Boston,MA"; // Boston is default
  return (
    <Redirect
      to={`/field_offices/${location_code}/${slugify(location_label)}`}
    />
  );
}

function FieldOffices() {
  const { width } = useWindowDimensions();
  let { location_code } = useParams();

  const url = `${PUBLIC_URL}field_offices.json`;
  const { response, loading } = useFetch(url);
  const history = useHistory();

  let info = response.info;
  console.log(location_code, info);

  if (loading) {
    return <Loading />;
  }

  if (_.isEmpty(info)) {
    return null;
  }

  let options = _.sortBy(
    _.map(_.values(info), (value) => {
      return {
        value: value["localOfficeInfo"]["locationCode"],
        label: value["localOfficeInfo"]["fullName"],
      };
    }),
    "label"
  );

  info = _.keyBy(info, "localOfficeInfo.locationCode");

  let fo_info = null;
  if (location_code) {
    let fo = info[location_code];
    let heat_map = JSON.parse(fo.cal_heat_map);

    let local = fo.localOfficeInfo;
    fo_info = (
      <div>
        <Card style={{ width: "18rem", marginBottom: "2rem" }}>
          <Card.Body>
            <Card.Title>{local.fullName}</Card.Title>
            <Card.Subtitle className="mb-2 text-muted">
              {fo.districtOfficeInfo.locationCode ===
              fo.localOfficeInfo.locationCode
                ? "District Office"
                : "Field Office"}
            </Card.Subtitle>
            <Card.Text>
              {local.address}
              <br />
              {local.city}, {local.state}
            </Card.Text>
          </Card.Body>
        </Card>
        {!fo.i485_times ? null : (
          <>
            <h4>Green Card Processing Times</h4>
            <p>
              {local.fullName} has approved{" "}
              <strong>{fo.i485_times.size} Green Cards</strong> in the past 90
              days, which makes it a{" "}
              {_.replace(fo.i485_times.size_label, "xsmall", "very small")}{" "}
              Field Office.
            </p>
            <p>
              The current median processing time is{" "}
              <strong>{fo.i485_times.median.toFixed(1)} months</strong>, which
              means that 50% of approvals were faster than that, and 50% were
              slower. This is an estimate for an average case for this Field
              Office.
            </p>
            <p>
              The current Top 90% processing time is{" "}
              <strong>{fo.i485_times.max.toFixed(1)} months</strong>, which
              means that 90% of approvals were faster than that, and 10% were
              slower. This is an estimate for a slow case for this Field Office.
            </p>
            <p>
              Processing Times indicate Form I-485 approvals within the past 90
              days, measured from estimated receipt date to approval date.
            </p>
            <h5>In Rankings:</h5>
            <Alert variant="primary">
              <LinkContainer to={`/rankings/${fo.size_label}`}>
                <Button className="black" variant="link">
                  <strong>#{fo.size_relative_ranking}</strong> of{" "}
                  {_.upperFirst(
                    _.replace(fo.size_label, "xsmall", "Very Small")
                  )}{" "}
                  Field Offices
                </Button>
              </LinkContainer>
            </Alert>
            {_.isNull(fo.size_absolute_ranking) ? null : (
              <Alert variant="success">
                <LinkContainer to="/rankings">
                  <Button className="black" variant="link">
                    <strong>#{fo.size_absolute_ranking}</strong> of all Field
                    Offices
                  </Button>
                </LinkContainer>
              </Alert>
            )}
            <h4>Distribution of Processing Times</h4>
            <Plot
              data={[
                {
                  x: fo.value_sample,
                  type: "histogram",
                },
              ]}
              useResizeHandler={true}
              style={{ width: "100%", height: "100%" }}
              config={{ displayModeBar: false, showTips: false }}
              layout={make_layout(
                {
                  margin: { t: 0 },
                  xaxis: { title: "Processing Time in months" },
                  yaxis: { title: "Green Cards approved" },
                },
                width,
                "fo_distplot"
              )}
            />
            <h4>Which Receipt Dates are currently being processed?</h4>
            <div style={{ overflowX: "scroll" }}>
              <Plot
                data={heat_map.data}
                style={{ minWidth: 750, width: "100%" }}
                // layout={make_layout(heat_map.layout, width)}
                layout={heat_map.layout}
                config={{ displayModeBar: false, showTips: false }}
              />
            </div>
            <p>
              This chart shows which Receipt dates for I-485 applications have
              been approved in the last 90 days. This allows you to see roughly
              when cases that are currently being processed have been filed.
              Take note that some I-485 applications (such as employment-based
              ones) may process faster than others.
            </p>
          </>
        )}
      </div>
    );
  }

  return (
    <WidthLimitedLayout>
      <h1>Field Offices</h1>
      <Form>
        <Form.Group controlId="exampleForm.SelectCustomSizeLg">
          <Form.Label>
            Select your Field Office (
            <a
              target="_blank"
              rel="noreferrer"
              href="https://www.uscis.gov/about-us/find-a-uscis-office/field-offices#district-search-form"
            >
              Which is my Field Office?
            </a>
            )
          </Form.Label>
          <Form.Control
            as="select"
            size="lg"
            defaultValue={location_code}
            onChange={(event) => {
              let value = event.target.value;
              let label = info[value].localOfficeInfo.fullName;
              localStorage.setItem("fieldOfficeValue", value);
              localStorage.setItem("fieldOfficeLabel", label);

              history.push(`/field_offices/${value}/${slugify(label)}`);
            }}
            custom
          >
            {_.map(options, (opt) => (
              <option key={opt.value} value={opt.value}>
                {opt.label}
              </option>
            ))}
          </Form.Control>
        </Form.Group>
      </Form>

      {fo_info}
      {DISCLAIMER}
    </WidthLimitedLayout>
  );
}

export { FieldOfficesRedirect, FieldOffices };
