import React from "react";
import SparkLines from "./SparkLines.js";

import _ from "lodash";
import { LinkContainer } from "react-router-bootstrap";
import { Table, Container, Row, Col } from "react-bootstrap";
import { Plot, DISCLAIMER } from "./Statics.js";
import { make_layout, WidthLimitedLayout } from "./util.js";
import ReactMarkdown from "react-markdown";

let pct = (i, prec = 2) => (i * 100).toFixed(prec) + "%";

function CurrentlyProcessingTable({ data }) {
  console.log(data);

  let dt = (value) => {
    if (!value) return "";
    let d = new Date(value);
    let ye = new Intl.DateTimeFormat("en", { year: "numeric" }).format(d);
    let mo = new Intl.DateTimeFormat("en", { month: "long" }).format(d);
    let da = new Intl.DateTimeFormat("en", { day: "2-digit" }).format(d);
    return `${mo} ${_.trimStart(da, "0")}, ${ye}`;
  };

  return (
    <Container>
      <Row>
        <Col>
          <ReactMarkdown
            children={_.trim(`
**I-130**:  
\`MSC:\` ${dt(data["I-130"]["MSC"])}  
\`LIN:\` ${dt(data["I-130"]["LIN"])}  
\`SRC:\` ${dt(data["I-130"]["SRC"])}  
\`WAC:\` ${dt(data["I-130"]["WAC"])}  
\`YSC:\` ${dt(data["I-130"]["YSC"])}  

**I-140**:  
\`LIN:\` ${dt(data["I-130"]["LIN"])}  
\`SRC:\` ${dt(data["I-130"]["SRC"])}  

**I-485**:  
\`MSC:\` ${dt(data["I-485"]["MSC"])}  
\`LIN:\` ${dt(data["I-485"]["LIN"])}  
\`SRC:\` ${dt(data["I-485"]["SRC"])}  
\`WAC:\` ${dt(data["I-485"]["WAC"])}  
\`EAC:\` ${dt(data["I-485"]["EAC"])}  
`)}
          />
        </Col>
        <Col>
          <ReactMarkdown
            children={_.trim(`
**I-131**:  
${dt(data["I-131"]["ALL"])}  

**I-765**:  
${dt(data["I-765"]["ALL"])}  

**N-400**:  
${dt(data["N-400"]["ALL"])}  

**I-751**:  
\`MSC:\` ${dt(data["I-751"]["MSC"])}  
\`EAC:\` ${dt(data["I-485"]["EAC"])}  
\`LIN:\` ${dt(data["I-751"]["LIN"])}  
\`SRC:\` ${dt(data["I-751"]["SRC"])}  
\`WAC:\` ${dt(data["I-751"]["WAC"])}  
\`YSC:\` ${dt(data["I-751"]["YSC"])}  
`)}
          />
        </Col>
      </Row>
    </Container>
  );
}

export default function Home({ data, last_updated, width, stat }) {
  stat = stat || "gcs_mailed";
  let raw = data.raw[stat];
  let rolling = data.rolling[stat];

  let displayNames = {
    cards_mailed: "Cards Mailed",
    gcs_mailed: "Green Cards Mailed",
    wps_mailed: "Work Permits Mailed",
    i130s_approved: "I-130s approved",
    approval_rate: "Approval Rate",
  };

  const LOOKBACK = 14;
  const LOOKBACK_AGO = LOOKBACK * 2;

  let plotData = [
    {
      x: _.keys(rolling),
      y: _.values(rolling),
      type: "scatter",
      mode: "lines",
      marker: { color: "red" },
      name: `${LOOKBACK}-day average`,
      line: { shape: "hvh" },
    },
    {
      type: "bar",
      x: _.keys(raw),
      y: _.values(raw),
      name: displayNames[stat],
    },
  ];

  let layout = {
    autosize: true,
    legend: {
      orientation: "h",
      yanchor: "bottom",
      y: 1.02,
      xanchor: "right",
      x: 1,
    },
    margin: {
      l: 50,
      r: 50,
      b: 50,
      t: 0,
      pad: 4,
    },
  };

  let sparkTdWidth = width < 1024 ? 100 : 180;
  let sparkWidth = width < 1024 ? 50 : 100;

  return (
    <WidthLimitedLayout>
      <div className="text-center">
        <h1>
          Immigration in the U.S.:
          <br />
          Latest Processing Times
        </h1>
        <div>Updated {last_updated}</div>
      </div>
      <div>
        <div style={{ height: 300 }}>
          <Plot
            data={plotData}
            useResizeHandler={true}
            style={{ width: "100%", height: "100%" }}
            layout={make_layout(layout, width, "home_plot")}
            config={{ displayModeBar: false, showTips: false }}
          />
        </div>
        <Table size={width < 1024 ? "sm" : ""}>
          <thead>
            <tr>
              <th></th>
              <th>DAILY AVG.</th>
              <th>{LOOKBACK_AGO}-DAY CHANGE</th>
            </tr>
          </thead>
          <tbody>
            {_.map(displayNames, (displayName, stat_name) => {
              let stat_map = data.rolling[stat_name];

              let values = _.values(stat_map);
              let cur_value = _.nth(values, -1);
              let ago_value = _.nth(values, -LOOKBACK_AGO - 1);
              let change = cur_value / ago_value - 1;

              let formatted_cur_value =
                stat_name === "approval_rate"
                  ? pct(cur_value, 0)
                  : cur_value.toFixed(0);

              let change_plus = change >= 0 ? "+" : "";

              return (
                <tr key={stat_name}>
                  <td
                    style={
                      width < 768
                        ? {
                            letterSpacing: "-0.4px",
                            fontSize: "80%",
                            fontWeight: 400,
                          }
                        : null
                    }
                  >
                    <LinkContainer
                      exact
                      to={`/latest/${stat_name}`}
                      activeClassName="active"
                      className="stat-link"
                    >
                      <a>
                        <strong>{displayName}</strong>
                      </a>
                    </LinkContainer>
                  </td>
                  <td>
                    <strong>{formatted_cur_value}</strong>
                  </td>
                  <td style={{ position: "relative", minWidth: sparkTdWidth }}>
                    <span>
                      {change_plus}
                      {pct(change, 0)}
                    </span>
                    <span style={{ position: "absolute", left: 65, top: 7 }}>
                      <SparkLines
                        values={values}
                        lookback_ago={LOOKBACK_AGO}
                        ago_value={ago_value}
                        sparkWidth={sparkWidth}
                      />
                    </span>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </Table>
        <small>
          The daily average is calculated from the past {LOOKBACK} days. Data is
          updated every day in the mornings (ET). Data for the past 6 days is
          estimated from partial data, and might shift around a bit until it
          settles after 6 days.
        </small>
      </div>
      <hr />
      <div style={{ marginTop: 30 }}>
        <div className="text-center">
          <h3>Currently Processing</h3>
          <p>
            The below are median receipt dates for cases approved in the last 90
            days.
          </p>
        </div>
        <CurrentlyProcessingTable data={data.currently_processing_table} />
        <Container>
          <Row>
            <Col>
              <ReactMarkdown
                children={_.trim(`
**Legend:**  
\`MSC:\` National Benefits Center  
\`EAC:\` Vermont Service Center (used to be the Eastern Adjudication Center)  
\`WAC:\` California Service Center (used to be the Western Adjudication Center)  
\`LIN:\` Nebraska Service Center  
\`SRC:\` Texas Service Center (used to be the Southern Regional Center)  
\`YSC:\` Potomac Service Center  
            `)}
              />
            </Col>
          </Row>
        </Container>
      </div>
      {DISCLAIMER}
    </WidthLimitedLayout>
  );
}
