import _ from 'lodash';
import { Table } from "react-bootstrap";
export default function JSONTable({ columns, rows, ...other }) {
  return (
    <Table {...other}>
      <thead>
        <tr>
          {_.map(columns, (col) => {
            return <th key={col}>{col.toUpperCase()}</th>;
          })}
        </tr>
      </thead>
      <tbody>
        {_.map(rows, (row) => {
          return (
            <tr key={row[0]}>
              {_.map(columns, (col) => {
                return <td key={col}>{row[col]}</td>;
              })}
            </tr>
          );
        })}
      </tbody>
    </Table>
  );
}
