import _ from "lodash";

function trimLines(text) {
  return _.join(_.map(_.split(text, "\n"), _.trimStart), "\n");
}

function make_layout(layout, width, id) {
  let updates =
    width > 1024
      ? {}
      : {
          autosize: true,
          margin: {
            l: 0,
            t: 0,
            r: _.includes(id, "approval") ? 40 : 10,
          },
          showlegend: !_.includes(["status_by_month_num", "fo_distplot"], id),
          mapbox: { zoom: 2 },
          xaxis: { fixedrange: true },
          yaxis: { fixedrange: true },
          legend: {
            orientation: "h",
            yanchor: "bottom",
            y: 1.02,
            xanchor: "right",
            x: 1,
          },
        };

  return _.merge(layout || {}, updates);
}

function plot_visible(form, id, width) {
  if (width < 1024) {
    if (id === "fo_box_plot") {
      return false;
    }
  }
  return true;
}

function slugify(label) {
  return _.replace(_.replace(label, ", ", ","), / /g, "_");
}

function WidthLimitedLayout(props) {
  return (
    <div className="daddy container-fluid">
      <div className="row py-3">
        <div
          id="main"
          className="col-sm-12 offset-sm-0 col-lg-8 offset-lg-2 col-xl-6 offset-xl-3"
        >
          <div className="page-content" style={{ maxWidth: 960, margin: "0 auto" }}>
            {props.children}
          </div>
        </div>
      </div>
    </div>
  );
}

function WidthLimitedLayoutBigger(props) {
  return (
    <div className="daddy container-fluid">
      <div className="row py-3">
        <div
          id="main"
          className="col-sm-12 offset-sm-0"
        >
          <div className="page-content" style={{ maxWidth: 1200, margin: "0 auto" }}>
            {props.children}
          </div>
        </div>
      </div>
    </div>
  );
}

export { trimLines, make_layout, plot_visible, slugify, WidthLimitedLayout, WidthLimitedLayoutBigger };
