import React, { useState, useRef } from "react";
import jsonp from "jsonp";
import { YMInitializer } from "react-yandex-metrika";
import useWindowDimensions from "./useWindowDimensions.js";

import { HashRouter as Router } from "react-router-dom";
import { LinkContainer } from "react-router-bootstrap";
import {
  Navbar,
  Nav,
  Form,
  FormControl,
  Button,
  Container,
} from "react-bootstrap";
import Routes from "./Routes.js";

import { ALL_FORM_TYPES } from "./Statics.js";

import "./App.css";

const FORM_NAV_DISPLAY = {
  "I-485": "Green Cards",
  "I-765": "Work & Travel Cards",
};

function BottomNav() {
  const { width } = useWindowDimensions();
  console.log(localStorage.getItem("hideEmailSub"));
  let [hidden, setHidden] = useState(
    localStorage.getItem("hideEmailSub") === "true"
  );
  if (hidden) {
    return null;
  }
  let hide = () => {
    localStorage.setItem("hideEmailSub", "true");
    setHidden(true);
  };
  return (
    <Navbar fixed={width < 1024 ? null : "bottom"} bg="dark" variant="dark">
      <Container
        style={{
          justifyContent: "center",
          flexWrap: width < 1024 ? "wrap" : "no-wrap",
          paddingTop: width < 768 ? null : 10,
          paddingBottom: 10,
        }}
      >
        <Navbar.Text style={{ margin: "0 20px", color: "white" }}>
          Get a weekly email with new {width < 1024 ? "GC" : "Green Card"}{" "}
          Processing Times
        </Navbar.Text>
        <div id="nav-email-form">
          <EmailForm hide={hide} />
          <button type="button" className="close" onClick={hide}>
            <span aria-hidden="true">Ã—</span>
            <span className="sr-only">Close alert</span>
          </button>
        </div>
      </Container>
    </Navbar>
  );
}

function EmailForm({ hide }) {
  let email_ref = useRef(null);

  let onSubmit = (event) => {
    event.preventDefault();
    event.stopPropagation();
    let BASE =
      "//greencardly.us7.list-manage.com/subscribe/post-json?u=f4841bf500160eb3d55b575eb&amp;id=e7c0544b8e";
    jsonp(
      `${BASE}&EMAIL=${encodeURIComponent(email_ref.current.value)}`,
      {
        param: "c",
      },
      function (err, data) {
        if (err) {
          console.error(err.message);
        } else {
          alert(
            "Thanks for subscribing! :) To complete the subscription process, please click the link in the email we just sent you."
          );
          console.log(data);
          hide();
        }
      }
    );
  };

  return (
    <Form inline onSubmit={onSubmit}>
      <FormControl
        type="email"
        placeholder="Email"
        className="mr-sm-2"
        ref={email_ref}
      />
      <Button variant="outline-danger" type="submit">
        Subscribe
      </Button>
    </Form>
  );
}

export default function App() {
  return (
    <div>
      <Router>
        <div>
          <Navbar
            variant="dark"
            expand="lg"
            collapseOnSelect={true}
            className="navbar-for-brand"
          >
            <LinkContainer exact to="/">
              <Navbar.Brand>Greencardly</Navbar.Brand>
            </LinkContainer>
          </Navbar>
          <Navbar variant="dark" expand="lg" collapseOnSelect={true}>
            <Nav className="mr-auto">
              {ALL_FORM_TYPES.map((form) => {
                return (
                  <LinkContainer exact key={form} to={`/${form}`}>
                    <Nav.Link>{FORM_NAV_DISPLAY[form] || form}</Nav.Link>
                  </LinkContainer>
                );
              })}
              <LinkContainer exact key="field_offices" to="/field_offices">
                <Nav.Link>Field Offices</Nav.Link>
              </LinkContainer>
              <LinkContainer exact key="rankings" to="/rankings">
                <Nav.Link>Rankings</Nav.Link>
              </LinkContainer>
              <LinkContainer exact key="case_samples" to="/case_samples">
                <Nav.Link>Case Samples</Nav.Link>
              </LinkContainer>

              <LinkContainer exact key="about" to="/about">
                <Nav.Link>About</Nav.Link>
              </LinkContainer>
            </Nav>
          </Navbar>
          <BottomNav />

          <Routes />
        </div>
      </Router>
      <YMInitializer accounts={[71610160]} version="2" />
    </div>
  );
}
