import _ from "lodash";
import { Plot } from "./Statics.js";
export default function SparkLines({
  values,
  lookback_ago,
  ago_value,
  sparkWidth,
}) {
  let spark_values = _.slice(values, lookback_ago);
  spark_values = _.at(
    spark_values,
    _.range(0, lookback_ago, Math.floor(lookback_ago / 8))
  );
  spark_values = _.map(spark_values, (x) => x / ago_value);
  if (_.max(spark_values) > 2) {
    spark_values = _.map(spark_values, (x) => x * (2 / _.max(spark_values)));
  }
  return (
    <Plot
      data={[
        {
          y: spark_values,
          x: _.range(_.size(spark_values)),
          type: "scatter",
          mode: "lines",
          line: {
            color: "rgb(99, 99, 99)",
          },
        },
      ]}
      layout={{
        showlegend: false,
        width: sparkWidth,
        height: 20,
        margin: { l: 0, r: 0, t: 0, b: 0, pad: 0 },
        xaxis: { visible: false },
        yaxis: { visible: false, range: [0, 2] },
      }}
      config={{
        displayModeBar: false,
        showTips: false,
        staticPlot: true,
      }}
    />
  );
}
