import React, { useState } from "react";
import { useParams } from "react-router-dom";

import _ from "lodash";
import { Loading, PUBLIC_URL, DISCLAIMER } from "./Statics.js";
import useWindowDimensions from "./useWindowDimensions.js";
import useFetch from "./useFetch.js";
import ReadMore from "./ReadMore.js";
import { slugify, WidthLimitedLayout } from "./util.js";

import "./App.css";

import { Table as BootyTable, Form, Badge, Button } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import { useTable, useSortBy } from "react-table";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faSort,
  faSortUp,
  faSortDown,
} from "@fortawesome/free-solid-svg-icons";

function compareBasic(a, b) {
  return a === b ? 0 : a > b ? 1 : -1;
}
let sortByValueThenOrigIndex = (rowA, rowB, col) => {
  const a = rowA.values[col];
  const b = rowB.values[col];
  let result = compareBasic(a, b);
  return result === 0
    ? compareBasic(rowA.values.index, rowB.values.index)
    : result;
};

function Table({ columns, data, limit }) {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = useTable(
    {
      columns,
      data,
      initialState: {
        sortBy: [
          {
            id: "median",
            desc: false,
          },
        ],
      },
    },
    useSortBy
  );

  const firstPageRows = rows.slice(0, limit);

  return (
    <>
      <BootyTable size="sm" {...getTableProps()}>
        <thead>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              <th> #</th>
              {headerGroup.headers.map((column) => (
                // Add the sorting props to control sorting. For this example
                // we can add them into the header props
                <th {...column.getHeaderProps(column.getSortByToggleProps())}>
                  <FontAwesomeIcon
                    style={{ float: "right", marginLeft: 5, marginTop: 2 }}
                    icon={
                      column.isSorted
                        ? column.isSortedDesc
                          ? faSortDown
                          : faSortUp
                        : faSort
                    }
                  />
                  {column.render("Header")}
                  {/* Add a sort direction indicator */}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {firstPageRows.map((row, i) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}>
                <td>{i + 1}</td>
                {row.cells.map((cell) => {
                  return (
                    <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
      </BootyTable>
      <br />
      {Math.min(limit, rows.length) !== rows.length ? (
        <div>
          Showing the first {Math.min(limit, rows.length)} of {rows.length} rows
        </div>
      ) : null}
    </>
  );
}

export default function Rankings() {
  const { width } = useWindowDimensions();
  let { size } = useParams();
  const url = `${PUBLIC_URL}rankings.json`;
  const { response, loading } = useFetch(url);
  let initialState = _.isEmpty(size)
    ? {
        xsmall: false,
        small: true,
        medium: true,
        large: true,
      }
    : {
        xsmall: size === "xsmall",
        small: size === "small",
        medium: size === "medium",
        large: size === "large",
      };
  const [xsmall, setXSmall] = useState(initialState.xsmall);
  const [small, setSmall] = useState(initialState.small);
  const [medium, setMedium] = useState(initialState.medium);
  const [large, setLarge] = useState(initialState.large);

  if (loading) {
    return <Loading />;
  }
  const location_code_map = response.location_code_map;
  const columns = [
    {
      accessor: "fo_name",
      Cell: ({ value }) => (
        <LinkContainer
          to={`/field_offices/${location_code_map[value]}/${slugify(value)}`}
        >
          <Button className="tight dark" variant="link">
            {value}
          </Button>
        </LinkContainer>
      ),
      Header: "Name",
    },
    {
      accessor: "median",
      Cell: ({ value }) => (
        <div className="text-right">{value ? value.toFixed(1) : null}</div>
      ),
      Header: (
        <div style={{ float: "right" }} className="text-right">
          Median time in months
        </div>
      ),
      sortType: sortByValueThenOrigIndex,
    },
    {
      accessor: "max",
      Cell: ({ value }) => (
        <div className="text-right">{value ? value.toFixed(1) : null}</div>
      ),
      Header: (
        <div style={{ float: "right" }} className="text-right">
          Top 90% time in months
        </div>
      ),
      sortType: sortByValueThenOrigIndex,
    },
    {
      accessor: "size",
      Cell: ({ value }) => (
        <div className="text-right">{value ? value.toFixed(0) : null}</div>
      ),
      Header: (
        <div style={{ float: "right" }} className="text-right">
          GCs approved
        </div>
      ),
      sortType: sortByValueThenOrigIndex,
    },
    {
      accessor: "size_label",
      Cell: ({ value }) => (
        <div className="text-right">{SIZE_BADGE_MAP[value]}</div>
      ),
      Header: <div className="text-right"></div>,
      disableSortBy: true,
    },
  ];
  const SIZE_BADGE_MAP = {
    xsmall: <Badge variant="light">XS</Badge>,
    small: <Badge variant="success">S</Badge>,
    medium: <Badge variant="warning">M</Badge>,
    large: <Badge variant="danger">L</Badge>,
  };
  const sizes = _.filter([
    xsmall ? "xsmall" : null,
    small ? "small" : null,
    medium ? "medium" : null,
    large ? "large" : null,
  ]);
  let data = _.filter(response.info, (row) =>
    _.includes(sizes, row.size_label)
  );

  return (
    <WidthLimitedLayout>
      <div id="rankings">
        <h1>Rankings</h1>

        <div style={{ paddingBottom: 10 }}>
          <div>
            This table ranks Field Offices by how quickly different Field
            Offices approve Green Cards. Times are months between estimated
            I-485 receipt and approval. <br />
            The <strong>Median</strong> processing time is an estimate for an
            average case. It indicates a timeframe where 50% of approvals were
            faster than that, and 50% were slower. <br />
            The <strong>Top 90%</strong> processing time is an estimate for a
            slow case. It indicates a timeframe where 90% of approvals were
            faster than that, and 10% were slower. All data is for I-485
            approvals in the last 90 days. <br />
            Field Offices are grouped into 4 roughly equally sized groups (
            {SIZE_BADGE_MAP["large"]}
            {SIZE_BADGE_MAP["medium"]}
            {SIZE_BADGE_MAP["small"]}
            {SIZE_BADGE_MAP["xsmall"]}) depending on how many Green Cards
            they've approved recently.
          </div>
        </div>
        <Form>
          <Form.Label className="my-1 mr-2">Field Office Sizes:</Form.Label>

          <Form.Check
            inline
            type="checkbox"
            id="large"
            checked={large}
            onChange={(event) => setLarge(event.target.checked)}
            label={
              <span>
                {SIZE_BADGE_MAP["large"]}
                {width < 1024 ? "" : " Large"}
              </span>
            }
          />
          <Form.Check
            inline
            type="checkbox"
            id="medium"
            checked={medium}
            onChange={(event) => setMedium(event.target.checked)}
            label={
              <span>
                {SIZE_BADGE_MAP["medium"]}
                {width < 1024 ? "" : " Medium"}
              </span>
            }
          />
          <Form.Check
            inline
            type="checkbox"
            id="small"
            checked={small}
            onChange={(event) => setSmall(event.target.checked)}
            label={
              <span>
                {SIZE_BADGE_MAP["small"]}
                {width < 1024 ? "" : " Small"}
              </span>
            }
          />
          <Form.Check
            inline
            type="checkbox"
            id="xsmall"
            checked={xsmall}
            onChange={(event) => setXSmall(event.target.checked)}
            label={
              <span>
                {SIZE_BADGE_MAP["xsmall"]}
                {width < 1024 ? "" : " Very Small"}
              </span>
            }
          />
        </Form>

        <Table columns={columns} data={data} limit={width < 1024 ? 100 : 100} />
        {DISCLAIMER}
      </div>
    </WidthLimitedLayout>
  );
}
